
import header from '../public/header'
import footer from '../public/footer'
import consult from '../public/consult'
import nav from '../public/nav'
import towBanner from '../../assets/towBanner.jpg'

import about_car1 from '../../assets/about_car1.jpg'
import about_car2 from '../../assets/about_car2.jpg'
import about_car3 from '../../assets/about_car3.jpg'

import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
	
  components: {
  	headerV : header,
  	footerV : footer,
  	consultV : consult,
  	navV : nav, Swiper, SwiperItem
  },
  data () {
    return {
    	banner : [
    		{src : towBanner, desc : ' '}
    	],
    	introduce_con : '',
    	found_txt : [
    		{
    			time : '2016.10.01',
    			title : '',
    			con : 'PC'
    		},
    		{
    			time : '2015.05.18',
    			title : '',
    			con : '50'
    		},
    		{
    			time : '2013.10.10',
    			title : '',
    			con : ''
    		},
    	],
    	about_car : [
    		{
    			src : about_car1,
    			title : '',
    			titles : '',
    			desc : '',
				desc2 : ' ',
				desc3 : ' ',
				desc4 : ' ',
				desc5 : ' ',
				desc6 : ' ',
				desc7 : ' ',
			},
    		{
    			src : about_car2,
    			title : '  ',
    			titles : '', 
    			desc : ' 1966',
				desc2 : ' 1992',
				desc3 : ' 2013',
				desc4 : ' ',
				desc5 : ' ',
				desc6 : ' 2008>>201120112013',
				desc7 : ' ',
					    			
    		},
    		{
    			src : about_car3,
    			title : '',
    			titles : '',
    			desc : ' 1963',
				desc2 : ' 1987',
				desc3 : ' 1993',
				desc4 : ' ',
    			
    		},
    	],
    }
  },
  mounted () {
  	setTimeout(()=>{	
  		this.$refs.a[0].style.right="0"
  	},300)
  }
}




